@import url('../../shared/theme.css');

.library-body {
    width: 100%;
    height: 95%;
    padding: 3%;
    display: flex;
    flex-wrap: wrap;
    overflow-y: auto;
    justify-content: space-evenly;
    transition: all .2s ease;
}

.playlist-card {
    position: relative;
    width: 200px;
    height: 280px;
    border-radius: 20px;
    background-color: rgb(30, 42, 62);
    border: 1px solid rgba(54, 69, 98, .18);
    padding: .5%;
    margin: 1%;
    background: rgb(30, 42, 62);
    background: linear-gradient(75deg, rgb(40, 58, 88) 0%, rgba(54, 69, 98, 0) 100%);
    transition: all .2s ease;
    cursor: pointer;
}

.playlist-card:hover {
    transform: scale(1.02)
}

.playlist-card:hover .playlist-fade {
    opacity: 1;
}

.playlist-image {
    width: 100%;
    aspect-ratio: 1;
    border-radius: 15px;
}

.playlist-title {
    font-weight: 800;
    font-size: 16px;
    color: var(--text);
    margin: 10px 0;
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 2;
    line-clamp: 2;
    -webkit-box-orient: vertical;
}

.playlist-subtitle {
    font-weight: 400;
    font-size: 12px;
    margin: 0;
    color: var(--text2);
}

.playlist-fade {
    position: absolute;
    right: 0;
    bottom: 0;
    opacity: 0;
    width: 100%;
    height: 34%;
    border-radius: 20px;
    background: linear-gradient(180deg, rgba(54, 69, 98, 0) 10%, rgba(54, 69, 98, 1) 100%);
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 8%;
    transition: all .5s ease;
}