@import url('../../shared/theme.css');

.queue-container {
    width: 100%;
    height: 40%;
    border-radius: 30px;
    opacity: 1;
    background-image: repeating-radial-gradient(
        circle at 0 0,
        transparent 0,
        #3e61d242 40px
    ),
    repeating-linear-gradient(#4767d055, #4767d0);
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.queue {
    height: 85%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 80%;
}

.upNext {
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    text-align: left;
    margin: 10px 0;
}

.queue-list {
    height: 80%;
    width: 100%;
    overflow-y: auto;
    padding-right: .25rem;
}

.queue-item {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 5px 0;
    font-size: 14px;
    font-weight: 500;
    color: #fff;
    cursor: pointer;
    transition: all .2s ease;
}

.queue-item:hover {
    transform: scale(.95)
}

.queue-item p {
    margin: 0;
}

.track-name {
    width: 75%;
    text-overflow: ellipsis;
}

@media screen and (max-width: 1200px) {
    .queue-container {
        height: 100%;
    }

    .queue {
        width: 90%;
    }
}

